<style>
 	.card{
 		width: 300px;
 		height: 230px;
 		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		background: white;
		border-radius: 10px;
		padding: 20px;
		position: relative;
		margin: 30px 0px;
 	}


 	.card > * {
 		width: 100%;
 		z-index: 2;
 	}

 	.card b{
 		font-family: ik;
 		font-size: 20px;
 		padding-left: 10px;
 		box-sizing: border-box;
 		text-transform: uppercase;
 		font-weight: bold;
 		background: white;
 		box-shadow: 0px 10px 20px rgba(0,0,0,0.1);
 		padding: 20px;
 		border-radius: 10px;
 		position: absolute;
 		top: -20px;
 		width: auto;
 		left: -10px;
 		z-index: 1;
 	}

 	.card b:before{
 		content: "";
 		background: #6f7fc7;;
 		position: absolute;
 		border-radius: 10px 0px 100px 10px;
 		top: 0px;
 		left: 0px;
 		width: 50%;
 		height: 100%;
 		z-index:-1;
 	}

 	.card p {
 		text-align: center;
 		color: black;
 		height: 130px;
 		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
 		font-family: ik;
 		line-height: 1.5em;
 		font-size: 17px;
 	}

 	.card button{
 		width: 170px;
 		background: #6f7fc7;;
 		color: white;
 		border: 3px solid #6f7fc7;;
 		border-radius: 5px;
 		font-weight: bold;
 	}

 	.card button:hover{
 		cursor: pointer;
 	}

 	.card:hover > button,.card:active > button{
 		color: #6f7fc7;;
 		background: white;
 		border: 3px solid white;
 	}

 	.card:hover > b{
 		color: black;
 	}

 	.card:before{
 		background: #6f7fc7;;
 		border-radius: 10px;
 	}


</style>

<div class="card" class:hvr-bounce-to-right={cardInfo.bgToRight} class:hvr-bounce-to-bottom={cardInfo.bgToRight === void 0} class:hvr-bounce-to-left={!cardInfo.bgToRight}>
	<b>{cardInfo.title}</b>
	<p>{cardInfo.description}</p>
	<button on:click={goto}>
		see {cardInfo.title}
	</button>
</div>

<script>
	
	export let cardInfo = {};

	function goto(){
		(cardInfo?.location)? location.href = cardInfo?.location : 0;
	}

</script>