<style>
	.navbar{
		width: 100%;
		height: 50px;
		background: transparent;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		position: fixed;
		z-index: 4;
	}

	.navbar-shadow{
		box-shadow: 0px 10px 20px rgba(0,0,0,0.1);
		background: white;
	}

	.navbar-shadow2{
		background: white;
	}

	.navbar .navbar-start{
		width:20%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}

	.navbar .navbar-start:before{
 		content: "";
 		background: #6f7fc7;;
 		box-shadow: 0px 10px 20px rgba(0,0,0,0.1);
 		position: absolute;
 		border-radius: 0px 0px 100px 0px;
 		top: 0px;
 		left: 0px;
 		width: 35%;
 		height: 100%;
 		z-index:-1;
 	}

	.navbar .navbar-start b{
		font-size: 20px;
		color: white;
		font-family: ik;
	}

	.navbar .navbar-end{
		width: 80%;
		height: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}

	.navbar .navbar-end li{
		list-style: none;
		margin: auto 10px;
	}

	.navbar .navbar-end button{
		width: 100px;
		height: 70%;
		margin: auto 10px;
		background: none;
		color: #6f7fc7;;
		border: 3px solid #6f7fc7;;
		border-radius: 5px;
		font-weight: bold;
		font-family: ik;
	}

	.navbar .navbar-end li:hover{
		cursor: pointer;
	}

	.navbar .navbar-end button:hover{
		cursor: pointer;
	}

	.navbar .navbar-end button#burger{
		width: 50px;
		padding: 5px;
		display: none;
	}

	@media only screen and (max-width: 600px) {
	  .navbar .navbar-end > *{
	  	display: none;
	  }

	  .navbar .navbar-end button#burger{
	  	display: block;
	  }

	  .navbar .navbar-end{
			width: 80%;
			height: 100%;
			display: flex;
			justify-content: flex-end;
			align-items: center;
			flex-wrap: wrap;
			padding-right: 30px;
			box-sizing: border-box;
		}
	}

	.menu{
		width: 100%;
		padding: 10px;
		box-sizing: border-box;
		position: fixed;
		top: 50px;
		z-index: 3;
		background: white;
	}
	.menu > *{
		width: 100%;
	}

	.menu li{
		list-style: none;
		margin: 20px 10px;
	}

	.menu button{
		height: 40px;
		background: none;
		color: #6f7fc7;;
		border: 3px solid #6f7fc7;;
		border-radius: 5px;
		font-weight: bold;
		font-family: ik;
	}
</style>

<div bind:this={$navbar} class="navbar" class:navbar-shadow2={shwnvbr}>
	<div class="navbar-start">
		<b>
			Daber
		</b>
	</div>
	<div class="navbar-end">
		<li>Home</li>
		<li>Contact</li>
		<button>certificate</button>
		<button id="burger" on:click={shownavbar}>
			<i class="fas fa-bars"></i>
		</button>
	</div>
</div>
{#if shwnvbr}
	<div class="menu" transition:slide>
		<li>Home</li>
		<li>Contact</li>
		<button>certificate</button>
	</div>
{/if}


<script>
	import { slide } from 'svelte/transition';
	import {navbar,navbarShadow} from "./restore.js";

	let shwnvbr = false;

	let shownavbar = ()=>{
		shwnvbr = !shwnvbr;
		$navbarShadow = !$navbarShadow;

		if($navbarShadow){
			$navbar.style.boxShadow = "";
		}else{
			$navbar.style.boxShadow=`0px 10px 20px rgba(0,0,0,0.1)`
		}
	}

</script>