<script>
	import {navbar,navbarShadow} from "./restore.js";
	let this_self_element;
	window.onscroll=(args)=>{
		if($navbarShadow && args.path[1].window.scrollY >= 50){
			$navbar.style.boxShadow=``
			$navbar.style.backgroundColor=`white`
		}else if(args.path[1].window.scrollY >= 50){
			$navbar.style.boxShadow=`0px 10px 20px rgba(0,0,0,0.1)`
			$navbar.style.backgroundColor=`white`
		}else{
			$navbar.style.boxShadow=``
			$navbar.style.backgroundColor=``
		}

		let position = this_self_element?.getBoundingClientRect?.()?.top;
		let child = document.querySelectorAll(".info-list");
		let childText = document.querySelectorAll(".text-animate");
		let childCard = document.querySelectorAll(".card");

		childText?.forEach((i)=>{
			if(i?.getBoundingClientRect?.()?.top < innerHeight)
				i.style = ` opacity: 1.0;
							transform: scale(1.0);
							transition: all 0.5s ease-in-out;`;
			else
				i.style = ` opacity: 0.1;
							transform: scale(0.1);
							transition: all 0.5s ease-in-out;`;

		});

		childCard?.forEach((i)=>{
			if(i?.getBoundingClientRect?.()?.top < innerHeight-20)
				i.style = ` opacity: 1.0;
							transform: scale(1.0);
							transition: all 0.5s ease-in-out;`;
			else
				i.style = ` opacity: 0.1;
							transform: scale(0.1);
							transition: all 0.5s ease-in-out;`;

		});

		(position < innerHeight - innerHeight/4)? 
		(()=>{
			child?.forEach((i)=>{
				i.style = ` opacity: 1.0;
							transform: scale(1.0);
							transition: all 1.2s ease-in-out;`;
			});
		})(): 
		(()=>{
			child?.forEach((i)=>{
				i.style = ` opacity: 0.1;
							transform: scale(0.1);
							transition: all 1.2s ease-in-out;`;
			});
		})();
	}

</script>

<div class="list" bind:this={this_self_element}>
	<b class="text-animate">I am working with</b>
	<div class="info-list ">
		<div class="img">
			<i class="fab fa-html5" ></i>
		</div>
		<p>HTML</p>
	</div>
	<div class="info-list ">
		<div class="img">
			<i class="fab fa-css3-alt"></i>
		</div>
		<p>CSS</p>
	</div>
	<div class="info-list ">
		<div class="img">
			<i class="fab fa-js-square"></i>
		</div>
		<p>JAVASCRIPT</p>
	</div>
	<div class="info-list ">
		<div class="img">
			<i class="fab fa-node-js"></i>
		</div>
		<p>NODE JS</p>
	</div>
</div>

<style>

	.text-animate{
		opacity: 0.0;
		transform: scale(0.1);
		transition: all 1s ease-in-out;
	}

	.list{
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		background: white;
	}

	.list .info-list{
		width: 200px;
		height: 200px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}

	.list b{
		width: 100%;
		text-align: center;
		font-size: 30px;
		padding: 20px;
		box-sizing: border-box;
		font-family: ik;
		display: flex;
		justify-content: center;
		align-items: center;
		color: #6f7fc7;
		flex-wrap: wrap;
	}

	.list .info-list .img{
		width: 100px;
		height: 100px;
		border-radius: 100%;
		background: white;
		border:5px solid #6f7fc7;;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
		box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
	}

	.list .info-list .img i{
		font-size: 50px;
	}

	.list .info-list p{
		font-weight: 600;
	}

	@media only screen and (max-width: 600px) {
		.list .info-list{
			width: 130px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
		}
	}
</style>