<style>
	.card-list{
		width: 100%;
		display: flex;
		justify-content: space-around;
		align-items: center;
		flex-wrap: wrap;
		padding: 10px;
		box-sizing: border-box;
		margin-bottom: 50px;
	}

	.card-list h1{
		width: 100%;
		text-align: center;
		font-family: ik;
		font-size: 30px;
		font-weight: bold;
	}

	.card-list p{
		width: 100%;
		text-align: center;
		font-family: ik;
		font-size: 15px;
		font-weight: bold;
	}
</style>

<div class="card-list">
